@mixin dark-syntax {
  //  syntax-base16.monokai.dark.css

  .highlight,
  .highlight .w {
    color: #f8f8f2;
    background-color: #272822;
  }
  .highlight .err {
    color: #272822;
    background-color: #f92672;
  }
  .highlight .c,
  .highlight .cd,
  .highlight .cm,
  .highlight .c1,
  .highlight .cs {
    color: #75715e;
  }
  .highlight .cp {
    color: #f4bf75;
  }
  .highlight .nt {
    color: #f4bf75;
  }
  .highlight .o,
  .highlight .ow {
    color: #f8f8f2;
  }
  .highlight .p,
  .highlight .pi {
    color: #f8f8f2;
  }
  .highlight .gi {
    color: #a6e22e;
  }
  .highlight .gd {
    color: #f92672;
  }
  .highlight .gh {
    color: #66d9ef;
    background-color: #272822;
    font-weight: bold;
  }
  .highlight .k,
  .highlight .kn,
  .highlight .kp,
  .highlight .kr,
  .highlight .kv {
    color: #ae81ff;
  }
  .highlight .kc {
    color: #fd971f;
  }
  .highlight .kt {
    color: #fd971f;
  }
  .highlight .kd {
    color: #fd971f;
  }
  .highlight .s,
  .highlight .sb,
  .highlight .sc,
  .highlight .sd,
  .highlight .s2,
  .highlight .sh,
  .highlight .sx,
  .highlight .s1 {
    color: #a6e22e;
  }
  .highlight .sr {
    color: #a1efe4;
  }
  .highlight .si {
    color: #cc6633;
  }
  .highlight .se {
    color: #cc6633;
  }
  .highlight .nn {
    color: #f4bf75;
  }
  .highlight .nc {
    color: #f4bf75;
  }
  .highlight .no {
    color: #f4bf75;
  }
  .highlight .na {
    color: #66d9ef;
  }
  .highlight .m,
  .highlight .mf,
  .highlight .mh,
  .highlight .mi,
  .highlight .il,
  .highlight .mo,
  .highlight .mb,
  .highlight .mx {
    color: #a6e22e;
  }
  .highlight .ss {
    color: #a6e22e;
  }
}
